import React from "react";
import {
  Container,
  Row,
  Col,
} from "reactstrap";
import PotentialCandidates from 'components/candidates/PotentialCandidates';
import FeaturedRecordings from 'components/recordings/FeaturedRecordings';

function Bookmarks() {
  return (
    <Container className="mt--0" fluid>
      <h1 className="main-page-title">Bookmarks</h1>
      <Row>
        <Col xl="12">
          <Row>
            <div className="col">
              <FeaturedRecordings />
            </div>
          </Row>
        </Col>
      </Row>

      <Row>
        <Col xl="12">
          <Row>
            <div className="col">

              <PotentialCandidates />

            </div>
          </Row>
        </Col>
      </Row>
    </Container>
  )
}

export default Bookmarks;